import { useContext, useState } from "react";
import { createContext } from "react";
import axios from 'axios'

const AuthContext = createContext();

function ifExist(target) {
    return target ? target : false;
}
export function useAuth() {
    return useContext(AuthContext)
}

export const useAuthProvider = () => {
    const ls = window.localStorage;
    const [authed, setAuthed] = useState(ls.getItem('franceAssist-auth') ? true : false);
    const [authToken, setAuthToken] = useState(ifExist(ls.getItem('franceAssist-authToken')))
    const [authedInfo, setAuthedInfo] = useState(false)


    const apiInfos = authToken ? {
        baseApi: 'https://france-assist.fr',
        headerApi: {
            "User-Agent": 'france-assist-agent',
            "Authorization": 'Bearer ' + authToken
        }
    } : {
        baseApi: 'https://france-assist.fr',
        headerApi: {
            "User-Agent": 'france-assist-agent'
        }
    }
    return {
        authed,
        authToken,
        userRole: authedInfo ? authedInfo.role : '',
        apiInfos,
        authedInfo,
        setUserInfo(data) {
            setAuthedInfo(data)
            // cookie.set('stuud-authInfos', data)
        },
        getUserInfo() {
            if (authedInfo) {

            } else {
                axios.get(apiInfos.baseApi + '/user',
                    { headers: apiInfos.headerApi })
                    .then(res => {
                        // console.log(res.data)
                        setAuthedInfo(res.data)
                    })
                    .catch(err => console.log(err))
            }
        }
        ,
        setToken(token) {
            setAuthToken(token)
            ls.setItem('franceAssist-authToken', token)
        },
        login() {
            setAuthed(true)
            ls.setItem('franceAssist-auth', true)
        },
        logout() {
            ls.removeItem('franceAssist-auth')
            ls.removeItem('franceAssist-authToken')
            setAuthed(false)
        },
    }
}

export const AuthProvider = ({ children }) => {
    const auth = useAuthProvider();

    return <AuthContext.Provider value={auth}>
        {children}
    </AuthContext.Provider>
}


